import { Collapse } from "antd";
import { CSelect } from "@components";

const CFixAssetReportSorting = ()=>{
    const {Panel} = Collapse;

    return (
      <>
         <Collapse defaultActiveKey={["0"]} style={{ marginTop: 10 }}>
            <Panel header="Report Sorting" key="0">
               <CSelect
                  label="Sort Field 1"
                  name="sort_field_1"
                  id="sort_field_1"
                  autoFocus
               />

               <CSelect
                  label="Sort Field 2"
                  name="sort_field_2"
                  id="sort_field_2"
                  autoFocus
               />

               <CSelect
                  label="Sort Field 3"
                  name="sort_field_3"
                  id="sort_field_3"
                  autoFocus
               />
            </Panel>
         </Collapse>
      </>
    )
}

export default CFixAssetReportSorting;